oreo debe ser registrado mínimo dos veces al día, y cualquier desviación debe documentarse e investigarse, aplicando las acciones correctivas pertinentes.")]),_vm._m(6),_vm._m(7),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Tanto la temperatura como la humedad deben ser monitoreadas y registradas periódicamente mediante formatos físicos o sistemas digitales. Estos registros deben conservarse durante un tiempo mínimo determinado por la normativa institucional o nacional, y deben estar disponibles para auditorías o inspecciones por parte de entidades como el INVIMA.")]),_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-down"}},[_vm._v("La correcta documentación de estos datos permite demostrar que se cumplen las condiciones exigidas para el almacenamiento de cada producto, y constituye una evidencia clave dentro del sistema de gestión de calidad del servicio farmacéutico.")]),_c('separador'),_vm._m(8),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("La distribución interna del almacén de productos farmacéuticos responde a principios de organización, seguridad, eficiencia operativa y cumplimiento normativo. Un diseño adecuado del espacio no solo facilita el acceso y control de los medicamentos, sino que también reduce los riesgos de errores, contaminación cruzada y pérdidas por vencimiento o deterioro. A continuación, se describen los objetivos de una buena distribución interna:")]),_vm._m(9),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("En este punto es indispensable identificar las principales zonas funcionales del almacén. La siguiente tabla presenta las zonas clave de un almacén farmacéutico, su función y ejemplos prácticos:")]),_vm._m(10),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("Para garantizar una operación segura y eficiente en el almacén farmacéutico, es fundamental implementar ciertas medidas que favorezcan la organización, el acceso y la conservación de los productos. Estas recomendaciones prácticas contribuyen a minimizar errores, facilitar el control y optimizar el espacio disponible, siempre en cumplimiento de la normativa vigente y las Buenas Prácticas de Almacenamiento. A continuación, se presentan algunas pautas clave para una distribución interna adecuada:")]),_vm._m(11),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("La señalización es clave para garantizar la seguridad del personal y la correcta ubicación de los productos. Toda zona, estante o contenedor debe estar claramente identificado con rótulos visibles, legibles y permanentes. A continuación, algunos ejemplos de rótulos útiles:")]),_vm._m(12),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("La señalización dentro del almacén es un elemento clave para garantizar la seguridad, la identificación rápida de zonas y la prevención de riesgos. El uso de colores estandarizados facilita la orientación del personal y el cumplimiento de las normativas vigentes. A continuación, se presentan los colores recomendados para la señalización, según las normas de seguridad industrial:")]),_vm._m(13),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("El flujo interno en un almacén farmacéutico debe estar organizado para minimizar errores y garantizar la trazabilidad. Idealmente, los productos deben seguir esta secuencia:")]),_vm._m(14),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("Las áreas especiales como neveras para productos refrigerados, cajas fuertes para psicotrópicos y estupefacientes, y zonas de disposición final deben estar claramente diferenciadas, señalizadas. Por ejemplo, en rojo para productos inservibles y ubicadas de forma que no interfieran con el flujo general. Se recomienda el uso de flechas verdes en el piso o paredes para indicar los flujos de desplazamiento y rótulos grandes y visibles para identificar cada zona del almacén. Esta organización mejora la seguridad, facilita las inspecciones y cumple con los lineamientos de las Buenas Prácticas de Almacenamiento (BPA).")]),_vm._m(15),_c('separador'),_vm._m(16),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("Para garantizar la trazabilidad, seguridad y conservación de los productos, el almacén debe contar con zonas específicas, entre las cuales se destacan:")]),_c('div',{staticClass:"row justify-content-center align-items-center mb-5",attrs:{"data-aos":"flip-up"}},[_c('SlyderF',{attrs:{"columnas":"col-lg-6 col-xl-4"}},[_c('div',{staticClass:"tarjeta tarjeta--BG08 p-4"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-lg-6 col-sm-4 col-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img22.svg"),"alt":"AvatarTop"}})])]),_c('h4',{staticClass:"text-center"},[_vm._v("Zona de recepción")]),_c('p',[_vm._v("Donde se realiza la verificación documental y física de los productos recibidos.")])]),_c('div',{staticClass:"tarjeta tarjeta--BG08 p-4"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-lg-6 col-sm-4 col-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img23.svg"),"alt":"AvatarTop"}})])]),_c('h4',{staticClass:"text-center"},[_vm._v("Zona de cuarentena")]),_c('p',[_vm._v("Destinada a productos en espera de validación o que requieren revisión adicional.")])]),_c('div',{staticClass:"tarjeta tarjeta--BG08 p-4"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-lg-6 col-sm-4 col-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img24.svg"),"alt":"AvatarTop"}})])]),_c('h4',{staticClass:"text-center"},[_vm._v("Zona de productos próximos a vencer")]),_c('p',[_vm._v("Permite aplicar el principio FEFO (First Expired, First Out).")])]),_c('div',{staticClass:"tarjeta tarjeta--BG08 p-4"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-lg-6 col-sm-4 col-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img25.svg"),"alt":"AvatarTop"}}